import React from 'react';


// css
import styles from "../../styles/components/Header.module.css";

const Header: React.FC = () => {
    const router = useRouter();

    return (
        <header className={styles.header}>

            <img src="assets/logo.svg" alt="logo do Centro Acadêmico de Ciência da Computação - UFPB" onClick={() => router.push("/")}/>

            <div className={styles.header_text}>
                <h1 className="website_title"   >Centro Acadêmico de Ciência da Computação</h1>
                <h2 className="website_subtitle">Universidade Federal da Paraíba - Centro de Informática</h2>

                <div className={styles.header_university_logos}>
                    <img src="assets/ufpb_logo.svg" alt="university_logo"></img>
                    <img src="assets/ci_logo.svg" alt="center_logo"></img>
                </div>
            </div>
        
        </header>
    )
}

export default Header;